import React from "react";
import Form from "../common/form";
import Joi from "joi-browser";
import auth from "../../services/authService";
class ForgotPass extends Form {
  state = {
    step: 1,
    data: { nationalCode: "", resetCode: "" },
    errors: {},
  };
  schema = {
    nationalCode: Joi.string()
      .required()
      .label(" ")
      .regex(/^[0-9]+$/, "")
      .min(2)
      .max(10)
      .error((errors) => this.ErrorsLang(errors)),
    resetCode: Joi.string()
      .label(" ")
      .regex(/^[0-9]+$/, "")
      .min(11)
      .max(11)
      .allow("")
      .error((errors) => this.ErrorsLang(errors)),
  };
  doSubmit = async () => {
    try {
      const { data, step } = this.state;
      if (step === 1) {
        await auth.sendPassResetCode(data.nationalCode);
        this.setState({ step: step + 1 });
      } else {
        await auth.passReset(data.nationalCode, data.resetCode);
        window.location = process.env.PUBLIC_URL + "/";
      }
    } catch (ex) {
      console.log(ex.response);
      if (ex.response && ex.response.status === 401) {
        const errors = { ...this.state.errors };
        errors.username = ex.response.data.detail; //translate it
        this.setState({ errors });
      }
    }
  };
  render() {
    const { step } = this.state;
    return (
      <div className="form-login d-flex align-items-center mt-4 pt-5">
        <div className="card col-lg-4 col-md-5 col-sm-7 col-10 mx-auto mt-5 px-5 pb-5">
          <span className="text-center login-icon mt-n5">
            <i className="fas fa-user bg-white"></i>
          </span>
          <h4 className="text-center text-muted pt-3">  </h4>
          {step === 1 && (
            <form onSubmit={this.handleSubmit}>
              {this.renderInput("nationalCode", " ")}
              <div className="text-center pt-4">
                {this.renderButton(" ", "mt-3")}
              </div>
            </form>
          )}
          {step > 1 && (
            <form onSubmit={this.handleSubmit}>
              <span className="d-block small text-center font-weight-bold text-secondary py-3">
                        
              </span>
              {this.renderInput("resetCode", " ")}
              <span className="d-block small text-danger text-center font-weight-bold">
                              
                
              </span>
              <div className="text-center pt-4">
                {this.renderButton("   ", "mt-3")}
              </div>
            </form>
          )}
        </div>
      </div>
    );
  }
}

export default ForgotPass;
